.link {
    padding: 10px;
    text-decoration: none;

}

.circle {
    margin-top: 12px;
    width: 20px;
    height: 20px;
    border-radius: 100%;
    background-color: #D8000C;
}

.nav {
    display: flex;
    width: 260px;
    transition-duration: 500ms;
    transform: translate(-220px);

}

.nav:hover{
    transition-duration: 500ms;
    transform: translate(0px);
}