.default {
    text-align: center;
    font-size: 13px;
    text-decoration: none;
    font-weight: 700;
    padding: 3px 6px;
    background: #eaeef1;
    display: block;
    width: 60px;
    margin: 20px auto;
    background-image: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, .1));
    border-radius: 3px;
    color: rgba(0, 0, 0, .6);
    text-shadow: 0 1px 1px rgba(255, 255, 255, .7);
}

.default:hover, a.butt.hover {
    background: #fff;
}
.default:active, a.butt.active {
    background: #d0d3d6;
    background-image: linear-gradient(rgba(0, 0, 0, .1), rgba(0, 0, 0, 0));
    box-shadow: inset 0 0 2px rgba(0, 0, 0, .2), inset 0 2px 5px rgba(0, 0, 0, .2), 0 1px rgba(255, 255, 255, .2);
}
.red {
    text-align: center;
    font-size: 13px;
    text-decoration: none;
    font-weight: 700;
    padding: 3px 6px;
    background: red;
    display: block;
    width: 60px;
    margin: 20px auto;
    background-image: linear-gradient(rgba(255, 0, 0, 0), rgba(255, 0, 0, .1));
    border-radius: 3px;
    color: rgba(0, 0, 0, .6);
    text-shadow: 0 1px 1px rgba(255, 255, 255, .7);
}

.red:hover, a.butt.hover {
    background: rgb(180,0,0);
}
.red:active, a.butt.active {
    background: rgb(180,0,0);
    background-image: linear-gradient(rgba(0, 0, 0, .1), rgba(0, 0, 0, 0));
    box-shadow: inset 0 0 2px rgba(0, 0, 0, .2), inset 0 2px 5px rgba(0, 0, 0, .2), 0 1px rgba(255, 255, 255, .2);
}